.champbox02 {
  color: #ffffff;
  max-width: 1600px;
  margin: 0 auto;
  .cmpimgBack {
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;

    .backimg {
      width: 100%;
      height: 100%;
      filter: blur(8px) brightness(0.4);
      position: fixed;
      left: 0;
      top: 0;
      transform: scale(1.1);
      z-index: -100;
      animation: 3000ms cubic-bezier(0.215, 0.61, 0.355, 1) 500ms 1 normal both
        running;
    }
  }
  ul {
    padding: 0;
    li {
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      p:nth-of-type(1) {
        font-size: 40px;
      }
      p:nth-of-type(2) {
        font-size: 30px;
      }
    }
    .textbox01 {
      display: flex;
      width: 100%;
      justify-content: center;
      align-items: center;
      span {
        width: 33%;
        border: 1px solid #fff;
        border-radius: 15px;
        text-align: start;
        padding: 5px;
      }
    }
  }
}
h2 {
  text-align: center;
  font-size: 40px;
  color: #fff;
}
.champbox03 {
  color: #ffffff;
  max-width: 87.5rem;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  .skillbox01 {
    width: 100%;
  }
  .relativebox {
    border: 1px solid #fff;
    border-radius: 25px;
    width: 100%;
    height: 300px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    
    .textbox01 {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      .textbox02{
        width: 100%;
      }

      .textbox03 {
        width: 100%;
      }
    }
  }
}
.skinbox{
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  overflow: hidden;
  height: 65vh;
  .skinshow01{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 30%;
    .skinshow02{
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      color: #fff;
      img{
        width: 30%;
      }
      p{
        width: 100%;
        text-align: center;
      }
    }
  }

    .skinshow03{
      width: 70%;
      position: relative;
      img{
        width: 100%;
        position: absolute;
      }
    }


}
